<template>
  <div class="register" style="background-color: #fff;">
    <div class="register-bar">
      <van-nav-bar :title="$t('noPass1')" left-arrow @click-left="linkHome"/>
    </div>
    <div class="submission">
      <div class="submission-img">
        <img src="../../../assets/examine/notPass.png"/>
      </div>
      <div class="tips">
        <div class="tips-two">{{$t('noPass1')}}</div>
        <div>{{$t('noPass2')}}</div>
        <div>{{$t('noPass3')}}</div>
      </div>
      <div class="again again1" @click="linkReg">{{$t('noPass4')}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'notPass',
  methods: {
    // 返回上级
    linkHome () {
      this.$router.push({
        name:'home'
      })
    },
    linkReg(){
      this.$router.push({
        name:'register'
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



